import {
  Component,
  ComponentFactoryResolver,
  ComponentRef,
  ElementRef,
  OnDestroy,
  OnInit,
  TemplateRef,
  ViewChild,
  ViewContainerRef,
  ViewEncapsulation
} from '@angular/core';

import {AdaptBusyTplComponent} from './busy-tpl.component';
import {AdaptBusyHostDirective} from './busy-ad-host.directive';
import {LoaderInOutType} from './busy-config';
import {
  AdaptRadarAngularCustomEventName,
  AdaptRadarAngularGenericDirective,
  AdaptRadarAngularService
} from '../common/radar/adapt-radar';

@Component({
  selector: 'adapt-busy, ng-busy',
  encapsulation: ViewEncapsulation.None,
  template: '<ng-template ng-busy-host></ng-template>',
  styles: [
    `
      .ng-busy-lineLoader .ng-busy-default-wrapper {
        align-items: stretch;
      }

      .ng-busy-lineLoader .ng-busy-default-wrapper .loader-container {
        width: 100%;
        padding: 0;
      }
    `
  ]
})
export class AdaptBusyComponent extends AdaptRadarAngularGenericDirective implements OnInit, OnDestroy {
  componentRef: ComponentRef<AdaptBusyTplComponent>;

  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  @ViewChild('container', {static: false}) container: any;
  @ViewChild(AdaptBusyHostDirective, {static: true}) adHost: AdaptBusyHostDirective;

  get message(): string {
    return this._message;
  }

  set message(message: string) {
    this._message = message;

    if (this.componentRef && this.componentRef.instance) {
      this.componentRef.instance.message = message;
    }
  }

  get sticky(): boolean {
    return this._sticky;
  }

  set sticky(sticky: boolean) {
    this._sticky = sticky;

    if (this.componentRef && this.componentRef.instance) {
      this.componentRef.instance.sticky = sticky;
    }
  }

  get wrapperClass(): string {
    return this._wrapperClass;
  }

  set wrapperClass(wrapperClass: string) {
    this._wrapperClass = wrapperClass;

    if (this.componentRef && this.componentRef.instance) {
      this.componentRef.instance.wrapperClass = wrapperClass;
    }
  }

  get inOutType(): LoaderInOutType {
    return this._inOutType;
  }

  set inOutType(inOutType: LoaderInOutType) {
    this._inOutType = inOutType;

    if (this.componentRef && this.componentRef.instance) {
      this.componentRef.instance.inOutType = inOutType;
    }
  }

  get offsetTop(): number | string {
    return this._offsetTop;
  }

  set offsetTop(offsetTop: number | string) {
    this._offsetTop = offsetTop;

    if (this.componentRef && this.componentRef.instance) {
      this.componentRef.instance.offsetTop = offsetTop as string;
    }
  }

  get type(): string {
    return this._type;
  }

  set type(type: string) {
    this._type = type;

    if (this.componentRef && this.componentRef.instance) {
      this.componentRef.instance.type = type;
    }
  }

  get value(): number {
    return this._percentageValue;
  }

  set value(value: number) {
    this._percentageValue = value;

    if (this.componentRef && this.componentRef.instance) {
      this.componentRef.instance.value = value;
    }
  }

  get determinate(): boolean {
    return this._determinate;
  }

  set determinate(value: boolean) {
    this._determinate = value;

    if (this.componentRef && this.componentRef.instance) {
      this.componentRef.instance.determinate = value;
    }
  }

  get loaderClass(): string {
    return this._loaderClass;
  }

  set loaderClass(value: string) {
    this._loaderClass = value;

    if (this.componentRef && this.componentRef.instance) {
      this.componentRef.instance.loaderClass = value;
    }
  }

  get loaderImg(): string {
    return this._loaderImg;
  }

  set loaderImg(value: string) {
    this._loaderImg = value;

    if (this.componentRef && this.componentRef.instance) {
      this.componentRef.instance.loaderImg = value;
    }
  }

  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  get content(): TemplateRef<any> {
    return this._content;
  }

  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  set content(value: TemplateRef<any>) {
    this._content = value;

    if (this.componentRef && this.componentRef.instance) {
      this.componentRef.instance.content = value;
    }
  }

  private _type: string;
  private _offsetTop: number | string;
  private _inOutType: LoaderInOutType;
  private _message: string;
  private _sticky: boolean;
  private _wrapperClass: string;
  private _percentageValue: number;
  private _determinate: boolean;
  private _loaderClass: string;
  private _loaderImg: string;
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  private _content: TemplateRef<any>;

  constructor(private viewContainerRef: ViewContainerRef,
              private componentFactoryResolver: ComponentFactoryResolver,
              _adaptRadarService: AdaptRadarAngularService,
              _elem: ElementRef<HTMLElement>) {
    super(_adaptRadarService, _elem, AdaptRadarAngularCustomEventName.AdaptBusy);
  }

  ngOnInit(): void {
    this.createDynamicTemplate();
  }

  ngOnDestroy(): void {
    if (this.componentRef) {
      this.componentRef.destroy();
    }
  }

  createDynamicTemplate(): void {
    const componentFactory = this.componentFactoryResolver.resolveComponentFactory(AdaptBusyTplComponent);

    this.viewContainerRef.clear();

    this.componentRef = this.adHost.viewContainerRef.createComponent(componentFactory, 0, null);
    this.componentRef.instance.message = this.message;
    this.componentRef.instance.wrapperClass = this.wrapperClass;
    this.componentRef.instance.sticky = this.sticky;
    this.componentRef.instance.offsetTop = this.offsetTop as string;
    this.componentRef.instance.determinate = this.determinate;
    this.componentRef.instance.value = this.value;
    this.componentRef.instance.loaderClass = this.loaderClass;
    this.componentRef.instance.loaderImg = this.loaderImg;
    this.componentRef.instance.content = this.content;

    if (this.inOutType) {
      this.componentRef.instance.inOutType = this.inOutType;
    }

    if (this.type) {
      this.componentRef.instance.type = this.type;
    }
  }
}
